@use '../foundation/foundation-compiler' as f;

.news-section{
    @include f.gradient(left, f.$gradient1);
    padding: 7rem 1rem;
    @include f.MD{
        padding: 16rem 0;
    }
}

.top-column{
    margin-bottom: 2rem;
    @include f.MD{
        margin-bottom: 4rem
    }
}

.top-column h1{
    @include f.display-1;
    color: f.$text-1;
}

.bottom-column{
    gap: 2rem;
}

.card{
    background-color: f.$secondary-5;
    border: none;
}

.card-body{
    background-color: f.$secondary-5;
    padding: 2.5rem 1rem;
}

.card-title{
    @include f.header-3;
    color: f.$text-1;
    margin-bottom: .5rem;
}

.card-text{
    @include f.body;
    color: f.$text-2;
    max-width: 320px;
    margin-bottom: 2rem;
}

.card-body .btn{
    @include f.body;
    background-color: f.$secondary-10;
    color: f.$text-1;
    padding: 1rem 1.5rem;
    transition: background-color 800ms ease;
}

.card-body .btn:hover{
    background-color: f.$primary-7;
}