$primary-7: #B84D15;
$primary-8: #994112;
$primary-9: #7B340E;
$primary-10: #61290B;

// Secondary
$secondary-1: #EFE6E6;
$secondary-2: #D9C2C2;
$secondary-3: #BA9191;
$secondary-4: #9A5E5E;
$secondary-5: #7C2E2E;
$secondary-6: #5F0000;
$secondary-7: #510000;
$secondary-8: #430000;
$secondary-9: #360000;
