// Background
$background-1: #FEFDFC;
$background-2: #FDF9F9;
$background-3: #FBF5F4;
$background-4: #F9F1EF;
$background-5: #F7ECEA;
$background-6: #F5E8E5;
$background-7: #D0C5C3;
$background-8: #AEA5A3;
$background-9: #8C8483;
$background-10: #6E6867;

// Text
$text-1: #EAEDEE;
$text-2: #CDD3D7;
$text-3: #A6B0B7;
$text-4: #7C8C96;
$text-5: #546976;
$text-6: #2F4858;
$text-7: #283D4B;
$text-8: #21333E;
$text-9: #1B2932;
$text-10: #152028;


// Gradient
$gradient1: $primary-10 0%, $primary-5 100%;
$gradient2: $background-8 0%, $background-1 100%; 


// Gradient Mixin
@mixin gradient($direction, $gradient) { 
    background: -webkit-linear-gradient($direction, $gradient);
    background: -moz-linear-gradient($direction, $gradient);
    background: -o-linear-gradient($direction, $gradient);
    background: linear-gradient($direction, $gradient);  
  }
