@use '../foundation/foundation-compiler' as f;

.sustainability-section{
    @include f.gradient(left, f.$gradient2);
    padding: 7rem 1rem;
    @include f.MD{
        padding: 16rem 0;
    }
}

.sustain-left-container h1{
    @include f.display-2;
    margin-bottom: 1rem;
    @include f.MD{
        margin-bottom: 2.5rem
    }
}

.sustain-left-container p{
    @include f.header-1;
    margin: 1.5rem 1rem;
    @include f.MD{
        margin: 2rem 0;
    }
}

.sustain-right-container{
    margin-bottom: 5rem;
    @include f.MD{
        margin-bottom: 0;
    }
}