@use '../foundation/foundation-compiler' as f;


.about-section{
    @include f.gradient(left, f.$gradient2);
    padding: 7rem 1rem;
    @include f.MD{
        padding: 16rem 0;
    }
}

.about-section img{
    margin-bottom: 2.5rem;
    width: 15rem;
    @include f.MD{
        margin-bottom: 1rem;
        width: 20rem;
    }
}

.about-section h1{
    @include f.display-2;
    margin-bottom: 2rem;
    @include f.MD{
        margin-bottom: 3rem;
    }
}

.about-section p{
    @include f.header-1;
    margin: 1rem 0;
    max-width: 750px;
    @include f.MD{
        margin: 2rem 0;
    }
}