@use '../foundation/foundation-compiler' as f;

footer{
    background-color: f.$secondary-6;
    padding: 2rem 1rem;
    @include f.MD{
        padding: 2rem 0;
    }
}

.top-left-column{
    gap: 1.5rem;
    margin-bottom: 1.5rem;
    @include f.MD{
        margin-bottom: 0;
    }
}

.top-left-column a{
    @include f.body;
    color: f.$text-1;
    text-decoration: none;
    transition: color 800ms ease;
}

.top-right-column a{
    color: f.$text-1;
    transition: color 800ms ease;
}

.top-right-column a:hover{
    color: f.$primary-6;
}

.top-right-column .bi{
    font-size: 1.5rem;
}

.top-left-column a:hover{
    color: f.$primary-6;
}

.top-right-column{
    gap: 1rem;
}

footer .bottom-column p{
    @include f.caption;
    color: f.$text-1;
}