@use '../foundation/foundation-compiler' as f;

nav{
    background-color: f.$secondary-6;
}

.navbar-brand img{
    width: 8rem;
    @include f.MD{
        width: 10rem;
    }
}

.nav-item{
    padding: 1rem ;
    @include f.LG{
        padding: 0 1rem;
    }
}

.nav-link{
    @include f.header-3;
    color: f.$text-1;
}

.nav-link:hover{
    color: f.$primary-5;
}