@use '../foundation/foundation-compiler' as f;

.hero-section{
    padding: 5rem 1rem;
    @include f.gradient(left bottom, f.$gradient1);
    @include f.MD{
        padding: 16rem 0;
        @include f.gradient(left, f.$gradient1);
    }
}

.hero-left-container h1{
   @include f.display-1;
   color: f.$text-1;
   max-width: 540px;
   margin-bottom: 1rem; 
   @include f.MD{
        margin-bottom: 3rem;
   }
   
}

.hero-left-container h2{
    @include f.header-1;
    color: f.$text-2;
    max-width: 450px;
    margin-bottom: 2.5rem;
    @include f.MD{
        margin-bottom: 3rem;
    }
}

.hero-left-container button{
    @include f.header-2;
    background-color: f.$secondary-6;
    color: f.$text-1;
    transition: background-color 800ms ease;
    gap: 0.5rem;
    padding: 1.5rem 3rem;
}

.hero-left-container button:hover{
    background-color: f.$secondary-10;
    color: f.$text-1;
}



.hero-right-container img{
    width: 350px;
    transform: rotate(10deg);
}
