@use './breakpoints' as b;
@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@200;300;400;600;800&display=swap');


// Default Line Height
$line-height-default: 120%;

// Default Font Family
$font-family-default: 'Libre Franklin', sans-serif;

// Font Weight
$f-weight-extra-light: 200;
$f-weight-light: 300;
$f-weight-normal: 400;
$f-weight-semi-bold: 600;
$f-weight-extra-bold: 800;

// Letter Spacing
$l-spacing-sub-text: 0.03em;
$l-spacing-caption: 0.02em;
$l-spacing-body: 0.01em;
$l-spacing-header-3: -0.01em;
$l-spacing-header-2: -0.02em;
$l-spacing-header-1: -0.03em;
$l-spacing-display-3: -0.04em;
$l-spacing-display-2: -0.05em;
$l-spacing-display-1: -0.06em;

// Default Font Size (Body)
$default-f-size-body: 16px;

// Web Font Size
$web-f-size-sub-text: 10px;
$web-f-size-caption: 13px;
$web-f-size-header-3: 20px;
$web-f-size-header-2: 25px;
$web-f-size-header-1: 31px;
$web-f-size-display-3: 38px;
$web-f-size-display-2: 47px;
$web-f-size-display-1: 58px;

// Mobile Font Size
$mobile-f-size-sub-text: 13px;
$mobile-f-size-caption: 14px;
$mobile-f-size-header-3: 18px;
$mobile-f-size-header-2: 20px;
$mobile-f-size-header-1: 22px;
$mobile-f-size-display-3: 25px;
$mobile-f-size-display-2: 28px;
$mobile-f-size-display-1: 31px;

// Typography Mixins
@mixin sub-text{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-extra-light;
    letter-spacing: $l-spacing-sub-text;
    font-size: $mobile-f-size-sub-text;
    @include b.MD{
        font-size: $web-f-size-sub-text;
    }
}

@mixin caption{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-light;
    letter-spacing: $l-spacing-caption;
    font-size: $mobile-f-size-caption;
    @include b.MD{
        font-size: $web-f-size-caption;
    }
}

@mixin body{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-normal;
    letter-spacing: $l-spacing-body;
    font-size: $default-f-size-body;
}

@mixin header-3{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-semi-bold;
    letter-spacing: $l-spacing-header-3;
    font-size: $mobile-f-size-header-3;
    @include b.MD{
        font-size: $web-f-size-header-3;
    }
}

@mixin header-2{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-semi-bold;
    letter-spacing: $l-spacing-header-2;
    font-size: $mobile-f-size-header-2;
    @include b.MD{
        font-size: $web-f-size-header-2;
    }
}

@mixin header-1{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-semi-bold;
    letter-spacing: $l-spacing-header-1;
    font-size: $mobile-f-size-header-1;
    @include b.MD{
        font-size: $web-f-size-header-1;
    }
}

@mixin display-3{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-extra-bold;
    letter-spacing: $l-spacing-display-3;
    font-size: $mobile-f-size-display-3;
    @include b.MD{
        font-size: $web-f-size-display-3;
    }
}

@mixin display-2{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-extra-bold;
    letter-spacing: $l-spacing-display-2;
    font-size: $mobile-f-size-display-2;
    @include b.MD{
        font-size: $web-f-size-display-2;
    }
}


@mixin display-1{
    line-height: $line-height-default;
    font-family: $font-family-default;
    font-weight: $f-weight-extra-bold;
    letter-spacing: $l-spacing-display-1;
    font-size: $mobile-f-size-display-1;
    @include b.MD{
        font-size: $web-f-size-display-1;
    }
}